.language-select-container {
  position: relative;
  width: 300px;
  max-width: 100%;
  margin: 1em 0;

  .language-select {
    display: block;
    font-size: 16px;
    color: #444;
    line-height: 1.3;
    padding: 0.6em 1.4em 0.5em 0.8em;
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    border-radius: 0.5em;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-repeat: no-repeat, repeat;
    background-position:
      right 0.7em top 50%,
      0 0;
    background-size:
      0.65em auto,
      100%;
  }

  .language-select-label {
    position: absolute;
    top: -0.6em;
    left: 0.8em;
    background-color: #fff;
    padding: 0 0.3em;
    font-size: 12px;
    color: #aaa;
    transition: color 0.3s ease;
  }

  &:focus-within .language-select-label {
    color: #6200ea; // Change this to the desired color when the select is focused
  }
}
